.catalog-item-view {
    display: grid;
    /* All content / Footer */
    grid-template-rows: 1fr 7rem;
    color: #ffffff;
    margin: 0 1rem 0 1rem;
    min-height: 100%;
}

.catalog-item-view-image {
    margin: auto;
    width: 7rem;
    height: 7rem;
    border-radius: 1.5rem;
}

.overriding-scam-mark {
    position: relative;
    height: 2rem;
    width: 2rem;
    top: -1rem;
    left: -1rem;
}

.catalog-item-view-title {
    font-size: 1.7rem;
    font-weight: 600;
    overflow: hidden;
    white-space: nowrap;
}

.catalog-item-view-caption {
    font-size: 1.2rem;
    color: #707070;
    font-weight: 500;
}

.catalog-item-view-header-content {
    margin-left: 1rem;
    display: grid;
    grid-template-rows: 2.5rem 1fr;
    text-align: left;
}

.catalog-item-view-header {
    margin-top: 1rem;
    display: grid;
    grid-template-columns: 8rem 1fr;
    align-items: start;
    justify-items: start;
}

.y-top {
    display: flex;
    justify-items: start;
}

.all-from-left {
    text-align: left;
}

.catalog-item-view-open-button {
    font-size: 1.2rem;
    margin-top: 1rem;
    background: #207bf9;
    border-radius: 1rem;
    height: 2rem;
    width: 5rem;
    font-weight: 500;
}

.catalog-item-view-description {
    font-size: 1.2rem;
    width: 100%;
    color: #707070;
    font-weight: 400;
    text-align: left;
}

.catalog-item-view-description-outer {
    display: flex;
    width: 100%;
}

.catalog-item-view-tags {
    font-size: 1.1rem;
    margin-top: 2rem;
    margin-bottom: 1rem;
    display: flex;
    gap: 1rem;
}

.catalog-item-view-tag {
    height: 1.5rem;
    padding: 0.5rem 1rem 0.5rem 1rem;
    background: #212121;
    border-radius: 0.5rem;
    width: auto;
    font-weight: 500;
}

.jetton-table {
    width: 100%;
    font-size: 1.2rem;
    font-weight: 400;
    margin-top: 2rem;
    display: grid;
    gap: 0.5rem;
}

.jetton-table-line {
    display: flex;
}

.jetton-table-fst {
    width: 50%;
    color: #707070;
}

.jetton-table-snd {
    width: 50%;
    color: #ffffff;
    text-align: right;
}

.jetton-description {
    width: 100%;
}

.red{
    background-color: #fc302c;
}

.button-rounded {
    border-radius: 1rem;
}

.catalog-item-view-scrollable {
    object-fit: cover;
    overflow-y: auto;
    overflow-x: hidden;
    padding-bottom: 2rem;
    max-height: calc(var(--app-height) - 13.1rem);
}