body {
    /* Common */
    margin: 0;
    background-color: #000000;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", sans-serif, "Inter";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    -webkit-tap-highlight-color: transparent;
    touch-action: none !important;
    
    /* Custom */
    --button-height : 3.5rem;
    --button-radius: calc(var(--button-height) / 2);
    --app-height: var(--tg-viewport-height);
}

@font-face {
    font-family: 'ArialRounded';
    src: local('ArialRounded'), url(fonts/arialroundedmtbold.ttf);
}