.catalog-select-button {
    background: none;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
    text-align: left;
    font-size: 1.5rem;
    color: #808080;
}

.catalog-select-button-selected {
    color: #ffffff;
}

.catalog-select-buttons {
    height: 3rem;
    margin: 1rem;
    width: 15rem;
    display: grid;
    gap: 3rem;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}