.catalog-entry {
    max-width: 100%;
    display: grid;
    grid-template-columns: 5rem 1fr 2rem;
    gap: 0.3rem;
    border-radius: 1rem;
    height: 4.5rem;
    margin: 0.4rem 0 0.4rem 0;
    width: calc(100%);
}

.catalog-entry-short-description {
    text-align: left;
    font-size: 0.85rem;
    color: #707070;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    font-weight: 500;
}

.catalog-entry-name {
    display: flex;
    font-size: 1.2rem;
    width: 100%;
    text-align: left;
    font-weight: 600;
}

.catalog-image {
    width: 3rem;
    height: 3rem;
    margin: auto;
    border-radius: 0.5rem;
    overflow: hidden;
}

.entry-scam-alert {
    width: 1.5rem;
    height: 1.5rem;
    position: relative;
    top: 0.05rem;
    left: 0.5rem;
}
