.catalog-search-outer {
    margin: 1rem 0 1rem 0 ;
}

.catalog-search-input {
    /* Font */
    width: calc(100% - 3em);
    height: 3rem;
    font-size: 1.2rem;
    color: #ffffff;
    padding-left : 1.5rem;
    
    /* Common styles */
    background: none;
    border-radius: 1.5rem;
    border-style: solid;
    border-width: 1px;
}

.catalog-search-input:focus {
    color: #ffffff;
    background: none;
    border: none;
    margin: 1px;
    font-weight: 500;
}

.catalog-search-input::placeholder {
    color: #505050;
}